#coupons {
  width: 600px;
  height: 500px;
  background: #fff;
  font-size: 9pt;
  outline: 0;
  border: 1px solid #808080;
}
.grid-header {
  border: 1px solid #808080;
  border-bottom: 0;
  border-top: 0;
  background: url("/img/header-bg.gif") repeat-x center top;
  color: #000;
  height: 24px;
  line-height: 24px;
}
.grid-header label {
  display: inline-block;
  font-weight: bold;
  margin: auto auto auto 6px;
}
.grid-header .ui-icon {
  margin: 4px 4px auto 6px;
  background-color: transparent;
  border-color: transparent;
}
.grid-header .ui-icon.ui-state-hover {
  background-color: #fff;
}
.grid-header #txtSearch {
  margin: 0 4px 0 4px;
  padding: 2px 2px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border: 1px solid #c0c0c0;
}
.options-panel {
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  border: 1px solid #c0c0c0;
  background: #f0f0f0;
  padding: 4px;
  margin-bottom: 20px;
  width: 320px;
  position: absolute;
  top: 0px;
  left: 650px;
}
.slick-cell.task-name {
  font-weight: bold;
  text-align: right;
}
.slick-cell.task-percent {
  text-align: right;
}
.slick-cell.cell-move-handle {
  font-weight: bold;
  text-align: right;
  border-right: solid #808080;
  background: #efefef;
  cursor: move;
}
.cell-move-handle:hover {
  background: #b6b9bd;
}
.slick-row.selected .cell-move-handle {
  background: #d5dc8d;
}
.slick-row .cell-actions {
  text-align: left;
}
.slick-row.complete {
  background-color: #dfd;
  color: #555;
}
.percent-complete-bar {
  display: inline-block;
  height: 6px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
}
input.editor-text {
  width: 100%;
  height: 100%;
  border: 0;
  margin: 0;
  background: transparent;
  outline: 0;
  padding: 0;
}
.ui-datepicker-trigger {
  margin-top: 2px;
  padding: 0;
  vertical-align: top;
}
input.editor-percentcomplete {
  width: 100%;
  height: 100%;
  border: 0;
  margin: 0;
  background: transparent;
  outline: 0;
  padding: 0;
  float: left;
}
.editor-percentcomplete-picker {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 100%;
  background: url("/img/pencil.gif") no-repeat center center;
  overflow: visible;
  z-index: 1000;
  float: right;
}
.editor-percentcomplete-helper {
  border: 0 solid #808080;
  position: absolute;
  top: -2px;
  left: -9px;
  background: url("/img/editor-helper-bg.gif") no-repeat top left;
  padding-left: 9px;
  width: 120px;
  height: 140px;
  display: none;
  overflow: visible;
}
.editor-percentcomplete-wrapper {
  background: #f5f5dc;
  padding: 20px 8px;
  width: 100%;
  height: 98px;
  border: 1px solid #808080;
  border-left: 0;
}
.editor-percentcomplete-buttons {
  float: right;
}
.editor-percentcomplete-buttons button {
  width: 80px;
}
.editor-percentcomplete-slider {
  float: left;
}
.editor-percentcomplete-picker:hover .editor-percentcomplete-helper {
  display: block;
}
.editor-percentcomplete-helper:hover {
  display: block;
}
select.editor-yesno {
  width: 100%;
  margin: 0;
  vertical-align: middle;
}
input.editor-checkbox {
  margin: 0;
  height: 100%;
  padding: 0;
  border: 0;
}
.slick-header-columns {
  background: url("/img/header-columns-bg.gif") repeat-x center bottom;
  border-bottom: 1px solid #c0c0c0;
}
.slick-header-column {
  background: url("/img/header-columns-bg.gif") repeat-x center bottom;
  border-right: 1px solid #c0c0c0;
}
.slick-header-column:hover,
.slick-header-column-active {
  background: #fff url("/img/header-columns-over-bg.gif") repeat-x center bottom;
}
.slick-headerrow {
  background: #fafafa;
}
.slick-headerrow-column {
  background: #fafafa;
  border-bottom: 0;
  height: 100%;
}
.slick-row.ui-state-active {
  background: #f5f7d7;
}
.slick-row {
  position: absolute;
  background: #fff;
  border: 0px;
  line-height: 20px;
}
.slick-row.selected {
  z-index: 10;
  background: #dfe8f6;
}
.slick-cell {
  padding-left: 4px;
  padding-right: 4px;
}
.slick-group {
  border-bottom: 2px solid #c0c0c0;
}
.slick-group-toggle {
  width: 9px;
  height: 9px;
  margin-right: 5px;
}
.slick-group-toggle.expanded {
  background: url("/img/collapse.gif") no-repeat center center;
}
.slick-group-toggle.collapsed {
  background: url("/img/expand.gif") no-repeat center center;
}
.slick-group-totals {
  color: #808080;
  background: #fff;
}
.slick-cell.selected {
  background-color: #f5f5dc;
}
.slick-cell.active {
  border-color: #808080;
  border-style: solid;
}
.slick-sortable-placeholder {
  background: #c0c0c0 !important;
}
.slick-row.odd {
  background: #fafafa;
}
.slick-row.ui-state-active {
  background: #f5f7d7;
}
.slick-row.loading {
  opacity: 0.5;
  filter: alpha(opacity=50);
}
.slick-cell.invalid {
  border-color: #f00;
  -moz-animation-duration: 0.2s;
  -webkit-animation-duration: 0.2s;
  -moz-animation-name: slickgrid-invalid-hilite;
  -webkit-animation-name: slickgrid-invalid-hilite;
}
@-moz-keyframes slickgrid-invalid-hilite {
  from {
    box-shadow: 0 0 6px #f00;
  }
  to {
    box-shadow: none;
  }
}
@-webkit-keyframes slickgrid-invalid-hilite {
  from {
    box-shadow: 0 0 6px #f00;
  }
  to {
    box-shadow: none;
  }
}
.slick-header.ui-state-default,
.slick-headerrow.ui-state-default {
  width: 100%;
  overflow: hidden;
  border-left: 0px;
}
.slick-header-columns,
.slick-headerrow-columns {
  position: relative;
  white-space: nowrap;
  cursor: default;
  overflow: hidden;
}
.slick-header-column.ui-state-default {
  position: relative;
  display: inline-block;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  height: 16px;
  line-height: 16px;
  margin: 0;
  padding: 4px;
  border-right: 1px solid #c0c0c0;
  border-left: 0px;
  border-top: 0px;
  border-bottom: 0px;
  float: left;
}
.slick-headerrow-column.ui-state-default {
  padding: 4px;
}
.slick-header-column-sorted {
  font-style: italic;
}
.slick-sort-indicator {
  display: inline-block;
  width: 8px;
  height: 5px;
  margin-left: 4px;
  margin-top: 6px;
}
.slick-sort-indicator-desc {
  background: url("/img/sort-desc.gif");
}
.slick-sort-indicator-asc {
  background: url("/img/sort-asc.gif");
}
.slick-resizable-handle {
  position: absolute;
  font-size: 0.1px;
  display: block;
  cursor: col-resize;
  width: 4px;
  right: 0px;
  top: 0;
  height: 100%;
}
.slick-sortable-placeholder {
  background: #c0c0c0;
}
.grid-canvas {
  position: relative;
  outline: 0;
}
.slick-row.ui-widget-content,
.slick-row.ui-state-active {
  position: absolute;
  border: 0px;
  width: 100%;
}
.slick-cell,
.slick-headerrow-column {
  position: absolute;
  border: 1px solid transparent;
  border-right: 1px dotted #c0c0c0;
  border-bottom-color: #c0c0c0;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  vertical-align: middle;
  z-index: 1;
  padding: 1px 2px 2px 1px;
  margin: 0;
  white-space: nowrap;
  cursor: default;
}
.slick-group-toggle {
  display: inline-block;
}
.slick-cell.highlighted {
  background: #87cefa;
  background: rgba(0,0,255,0.2);
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.slick-cell.flashing {
  border: 1px solid #f00 !important;
}
.slick-cell.editable {
  z-index: 11;
  overflow: visible;
  background: #fff;
  border-color: #000;
  border-style: solid;
}
.slick-cell:focus {
  outline: none;
}
.slick-reorder-proxy {
  display: inline-block;
  background: #00f;
  opacity: 0.15;
  filter: alpha(opacity=15);
  cursor: move;
}
.slick-reorder-guide {
  display: inline-block;
  height: 2px;
  background: #00f;
  opacity: 0.7;
  filter: alpha(opacity=70);
}
.slick-selection {
  z-index: 10;
  position: absolute;
  border: 2px dashed #000;
}
#zenbox_tab {
  border: solid 1px;
  cursor: pointer;
  position: fixed;
  top: 23%;
  z-index: 10000;
}
.ZenboxTabLeft {
  left: 0;
  background-position: right 0;
}
.ZenboxTabRight {
  right: 0;
  background-position: left 0;
}
#zenbox_tab:hover {
  background-color: #444;
}
#zenbox_tab img {
  display: block;
}
#zenbox_overlay {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 90000;
}
#zenbox_overlay * {
  color: #333;
  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  font-variant: normal;
  font-size: 12px;
  margin: 0;
  padding: 0;
}
#zenbox_container {
  height: 620px;
  margin: 0 auto;
  position: relative;
  text-align: left;
  width: 638px;
  z-index: 2;
}
#zenbox_container .zenbox_header {
  position: relative;
  text-align: right;
  width: auto;
  z-index: 100;
}
#zenbox_close {
  cursor: pointer;
  height: 46px;
  position: relative;
  top: 10px;
  width: 46px;
}
#zenbox_body {
  height: 900px;
  margin-top: -18px;
  position: relative;
  width: 615px;
}
#zenbox_scrim {
  background-color: #000;
  filter: alpha(opacity=42);
  -moz-opacity: 0.42;
  left: 0;
  position: absolute;
  opacity: 0.42;
  top: 0;
  width: 100%;
  z-index: 1;
}
.uk-notify-message a {
  color: #fff;
  text-decoration: underline;
}
